body {
  background-color: #ff0037;
  padding: 20px;
  margin: 0;
}



h1, h2, p, ul, li {
  font-family: sans-serif;
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  background-color: #000105;
  padding: 0;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
/* .content {
  background-color: rgb(22, 14, 245);
  padding: 20px;
} */
.content h2 {
  padding: 0;
  margin: 0;
  color: #FFF;
}
.content li {
  margin-bottom: 10px;
}
.active {
  background-color: #0022ff;
}
